<template>
  <div 
    class="game-card" 
    :class="cardTypeClass"
    @click="$emit('click', $event)"
    style="border: 2px solid lightblue; position: relative; border-radius: 12px; aspect-ratio: 2.5/3.5; display: flex; flex-direction: column; box-shadow: 3px 3px 8px rgba(0,0,0,0.2); background-color: #f8f8f8; overflow: hidden;"
  >
    <div style="position: absolute; top: -20px; left: 0; background-color: lightblue; padding: 2px 6px; font-size: 12px; color: #333; z-index: 1070;">GameCard</div>
    
    <!-- Card corner decoration (top left) -->
    <div style="position: absolute; top: 5px; left: 5px; font-size: 0.8rem; font-weight: bold; text-transform: uppercase;">
      {{ title.charAt(0) }}
      <div style="font-size: 1rem; margin-top: 2px;">{{ getSuitSymbol() }}</div>
    </div>
    
    <!-- Card corner decoration (bottom right) -->
    <div style="position: absolute; bottom: 5px; right: 5px; font-size: 0.8rem; font-weight: bold; text-transform: uppercase; transform: rotate(180deg);">
      {{ title.charAt(0) }}
      <div style="font-size: 1rem; margin-top: 2px;">{{ getSuitSymbol() }}</div>
    </div>
    
    <div class="game-card__header" style="border-top-left-radius: 12px; border-top-right-radius: 12px; padding: 10px; text-align: center;">
      <h3 style="margin: 0; font-size: 1.2rem; text-transform: capitalize;">{{ title }}</h3>
      <div v-if="subtitle" class="game-card__subtitle" style="font-size: 0.8rem; opacity: 0.8;">{{ subtitle }}</div>
    </div>
    
    <!-- Card border decoration -->
    <div style="position: absolute; top: 40px; left: 0; right: 0; bottom: 40px; border: 1px dashed rgba(0,0,0,0.1); border-radius: 8px; margin: 8px; pointer-events: none;"></div>
    
    <!-- Center suit symbol -->
    <div style="position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); font-size: 3rem; opacity: 0.1; pointer-events: none;">
      {{ getSuitSymbol() }}
    </div>
    
    <div class="game-card__body" style="flex-grow: 1; display: flex; align-items: center; justify-content: center; text-align: center; padding: 15px; z-index: 1;">
      <slot></slot>
    </div>
    
    <div v-if="$slots.footer" class="game-card__footer" style="border-bottom-left-radius: 12px; border-bottom-right-radius: 12px; padding: 10px; text-align: center;">
      <slot name="footer"></slot>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';
import { CardType } from '@/models/enums/cardTypes';
import { Season } from '@/models/enums/seasons';

export default defineComponent({
  name: 'GameCard',
  props: {
    title: {
      type: String,
      required: true
    },
    subtitle: {
      type: String,
      default: ''
    },
    cardType: {
      type: String as PropType<CardType>,
      required: true,
      validator: (value: string) => {
        return Object.values(CardType).includes(value as CardType);
      }
    },
    season: {
      type: String as PropType<Season | null>,
      default: null,
      validator: (value: string) => {
        return value === null || Object.values(Season).includes(value as Season);
      }
    }
  },
  computed: {
    cardTypeClass() {
      if (!this.cardType) return {};
      
      const cardTypeValue = this.cardType.toLowerCase();
      const classes = {
        [`game-card--${cardTypeValue.replace(/_/g, '-')}`]: true
      };
      
      if (this.cardType === CardType.SEASON && this.season) {
        classes[this.season.toLowerCase()] = true;
      }
      
      return classes;
    }
  },
  methods: {
    getSuitSymbol() {
      // Return different suit symbols based on card type
      switch(this.cardType) {
        case CardType.LANDSCAPE:
          return ''; // clubs
        case CardType.ANIMAL_COMPANION:
          return ''; // hearts
        case CardType.RESOURCE:
          return ''; // diamonds
        case CardType.CRAFTED_ITEM:
          return ''; // spades
        case CardType.SEASON:
          return ''; // star
        case CardType.CHARACTER:
          return ''; // diamond star
        default:
          return ''; // bullet
      }
    }
  },
  emits: ['click']
});
</script>

<style lang="scss" scoped>
// Styles are in the main.scss file
</style>
